.legend {
	background: #1d2336;
	position: fixed;
	width: 800px;
	height: 100%;
	right: -800px;
	opacity: 0;
	bottom: 0;
	color: #BDC4DB;
	z-index: 102;
	box-shadow: 0px 0px 40px 16px rgba(11, 16, 40, 0.94);
	overflow: auto;
	transition: all 200ms;
	    display: flex;
}

.legend.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.legend.open {
	right: 0;
	opacity: 1;
}

.legend__close {
	position: absolute;
	top: 20px;
  right: 20px;
	cursor: pointer;
	fill: #5872da;
}

.legend__section {
    margin-left: 70px;
    margin-top: 30px;
    max-width: 280px;
}

.legend__title {
    color: #858ead;
    font-size: 14px;
    margin-bottom: 10px;
}

.legend__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.legend__text {
    font-size: 12px;
    margin-left: 10px;
    line-height: 16px;
}

.legend__image {
    line-height: 0;
    min-width: 28px;
    min-height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.legend__image img {
    max-width: 20px;
}

.legend__image.arrow {
    margin-right: 10px;
}

.legend__image.arrow img {
    max-width: 56px;
}

.legend__image.last {
    margin-right: 10px;
}

.legend__image.last img {
    max-width: 56px;
}

.legend__right .legend__item{
	margin-bottom: 0;
}