.zoom {
	position: fixed;
	width: 320px;
	height: 56px;
	z-index: 4;
	bottom: 8px;
	right: 8px;
	background: #2b3250;
	display: flex;
	padding: 8px;
	border-radius: 8px;
	box-sizing: border-box;
}

.zoom.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.zoom.close {
  display: none;
}

.zoom.map__open {
	border-radius: 0 0 8px 8px;
	    width: 280px;
}

.zoom.map__open .zoom__mapicon {
	display: none;
}

.zoom__minus,
.zoom__plus,
.zoom__mapicon {
	width: 40px;
	height: 40px;
	border-radius: 8px;
	cursor: pointer;
}

.zoom__minus:hover,
.zoom__plus:hover,
.zoom__mapicon:hover {
	background: rgba(255, 255, 255, 0.1);
}

.zoom.light .zoom__minus:hover,
.zoom.light .zoom__plus:hover,
.zoom.light .zoom__mapicon:hover {
	background: rgba(41, 44, 67, 0.1);
}

.zoom__mapicon {
	
}

.map__close {
	position: absolute;
	right: 0;
	top: 0;
	cursor: pointer;
}

.zoom__scale {
	width: 168px;
	margin: 0 8px;
	padding: 12px 0;
}

.input-range__slider {
  appearance: none;
  background: #3f51b5;
  border: 1px solid #3f51b5;
  border-radius: 100%;
  cursor: pointer;
  display: block;
  height: 1rem;
  margin-left: -0.5rem;
  margin-top: -0.65rem;
  outline: none;
  position: absolute;
  top: 50%;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  width: 1rem; }
  .input-range__slider:active {
    transform: scale(1.3); }
  .input-range__slider:focus {
    box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2); }
  .input-range--disabled .input-range__slider {
    background: #cccccc;
    border: 1px solid #cccccc;
    box-shadow: none;
    transform: none; }

.input-range__slider-container {
  transition: left 0.3s ease-out; }

.input-range__label {
  color: #aaaaaa;
  font-family: "Helvetica Neue", san-serif;
  font-size: 0.8rem;
  transform: translateZ(0);
  white-space: nowrap; }

.input-range__label--min,
.input-range__label--max {
  bottom: -1.4rem;
  position: absolute; }

.input-range__label--min {
  left: 0; }

.input-range__label--max {
  right: 0; }

.input-range__label--value {
  position: absolute;
  top: -1.8rem; }

.input-range__label-container {
  left: -50%;
  position: relative; }
  .input-range__label--max .input-range__label-container {
    left: 50%; }

.input-range__track {
  background: #eeeeee;
  border-radius: 0.3rem;
  cursor: pointer;
  display: block;
  height: 0.3rem;
  position: relative;
  transition: left 0.3s ease-out, width 0.3s ease-out; }
  .input-range--disabled .input-range__track {
    background: #eeeeee; }

.input-range__track--background {
  left: 0;
  margin-top: -0.15rem;
  position: absolute;
  right: 0;
  top: 50%; }

.input-range__track--active {
  background: #3f51b5; }

.input-range {
  height: 1rem;
  position: relative;
  width: 100%; }

