.input-range__label--min {
    display: none;
}

.input-range__label--max {
    display: none;
}

.input-range__label--value {
    display: none;
}

.input-range__slider {
    width: 11px;
    height: 11px;
    margin-top: -7px;
    background: #859DE0;
    border: none;
}

.input-range__track {
    height: 3px;
    background: #5A6487;
}

.input-range__track.input-range__track--active {
    background: #859DE0;
}

.input-range__slider:active {
    transform: scale(1);
}

.zoom__map {
    z-index: 100;
    position: absolute;
    overflow: hidden;
    bottom: 64px;
    right: 8px;
    width: 280px;
    height: 145px;
    background: #2b3250;
    padding: 15px 15px 0 15px;
    border-radius: 8px 8px 0 0;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
}

.zoom__map.map__open{
	visibility: visible;
    opacity: 1;
}

.zoom__map.light{
	background: #fff;
	border-color: #fff;
}

.map__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
}

.map__overview{
	width: 215px;
	height: 130px;
	background: #1d2336;
}

.zoom__map.light .map__overview{
	background: #eef0f5;
}