.header {
	background: #2B3250;
	height: 58px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	z-index: 102;
	font-size: 14px;
	padding: 0 12px;
}

.header.light{
	background: #fff;
}

.header__left,
.header__right {
	display: flex;
	align-items: center;
}

.header__item {
	margin: 0 4px;
	height: 40px;
	padding: 0 10px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
}

.header__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.header.light .header__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.header__item img, .header__item svg {
	opacity: 0.4;
}

.header__item.active img, .header__item.active svg {
	opacity: 1;
}

.header__item input {
	background: transparent;
	font-family: 'Fira Sans';
	color: #fff;
	font-size: 14px;
	border: none;
	width: 23px;
}

.light .header__item input {
	color: #2b3250;
}

.header__item-zoom:after {
	content: '%';
}
