body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans';
}

* {
  outline: none;
}

.app{
  background: #1d2336;
  color: #fff;
  fill: #fff;
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: hidden;
}

.app.light{
  background: #fff;
  color: #2b3250;
  fill: #2b3250;
}

.diagram-component {
  background: #1d2336;
  position: absolute;
  top: 58px;
  bottom: 0;
  left: 0;
  right: 0;
}

.light .diagram-component {
  background: #eef0f5;
}

.Dropdown-control {
    padding: 10px 42px 10px 20px;
    border: none;
    background: transparent;
    color: #fff;
    cursor: pointer;
    border-radius: 8px;
}

.light .Dropdown-control {
    color: #2b3250;
}

.Dropdown-control:hover {
    background: rgba(255, 255, 255, 0.1);
}

.Dropdown-root.is-open .Dropdown-control {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px 8px 0 0;
}

.light .Dropdown-control:hover {
    background: rgba(41, 44, 67, 0.1);
}

.light .Dropdown-root.is-open .Dropdown-control {
    background: rgba(41, 44, 67, 0.1);
}

.Dropdown-menu {
    background: #29367A;
    color: #fff;
    border: none;
    border-radius: 0 0 8px 8px;
    padding: 12px 10px;
    width: auto;
    min-width: 100%;
}

.Dropdown-option {
    white-space: nowrap;
    padding: 8px 10px;
    border-radius: 6px;
    cursor: pointer;
    margin-bottom: 5px;
    color: #fff;
}

.Dropdown-option:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
}

.Dropdown-option.is-selected {
    background: #5872DA;
    cursor: default;
    color: #fff;
}

.light .Dropdown-menu {
    background: #f2f4f7;
    color: #2b3250;
}

.light .Dropdown-option {
    color: #2b3250;
}

.light .Dropdown-option.is-selected {
    background: rgba(41, 44, 67, 0.1);
    color: #2b3250;
}

.light .Dropdown-option:hover {
    background: rgba(41, 44, 67, 0.1);
    color: #2b3250;
}

.Dropdown-option:last-child {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.Dropdown-arrow-wrapper {
    position: absolute;
    top: 16px;
    right: 20px;
    height: 7px;
    width: 10px;
    line-height: 0;
    transition: transform 100ms;
}

.is-open .Dropdown-arrow-wrapper {
    transform: rotate(180deg);
}
