.potential-card {
	position: absolute;
	width: 300px;
	height: 333px;
	right: 88px;
	top: 94px;
	background: #2B3250;
	box-shadow: 0px 16px 40px rgba(11, 16, 40, 0.94);
	border-radius: 8px;
	z-index: 100;
	color: #fff;
	font-size: 14px;
	cursor: move;
}

.potential-card.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.potential-card.close {
	display: none;
} 

.potential-card:after {
	content: '';
	position: absolute;
	width: 6px;
	height: 200px;
	left: -3px;
	top: 20px;
	background: #F2696D;
	border-radius: 8px;
	z-index: 2;
}

.potential-card__title {
    padding: 19px 24px;
    color: #859DE0;
    font-size: 15px;
}

.potential-card__items {
	border-top: 1px solid rgba(88, 114, 218, 0.2);
}

.potential-card__item {
	padding: 12px 24px;
	cursor: pointer;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
	display: flex;
	align-items: center;
}

.potential-card__item:hover {
	background: rgba(88, 114, 218, 0.2);
}

.potential-card__item.active {
	background: rgba(88, 114, 218, 0.25);
}

.potential-card__icon {
    width: 36px;
}


.potential-card__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}